.portfolios-gellary{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
    position: relative;
    z-index: 99;
    margin: 50px 0;
}

.portfolios-gellary a{
    display: block;
    width: 31.59%;
}

.portfolios-img-box{
    width: 100%;
    height: 450px;
    transition: ease-in-out 3s;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    z-index: 99;
    box-shadow: 0px 6px 18px 2px rgb(0 0 0 / 32%);
    display: grid;
    place-items: center;
}

.portfolios-img-box::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    background-color: #4e3d1c;
    opacity: 0;
    transition: ease-in-out 3s;
}

.portfolios-img-box:hover::before{
    opacity: .1;
}

.portfolios-img-box:hover{
    background-position: bottom;
    box-shadow: none;
}

.visit_btn{
    background-color: #F6AE2D !important;
    font-family: 'Montserrat', sans-serif !important;
    font-size: 12px !important;
    letter-spacing: 2px !important;
    color: #fff !important;
    padding: 10px 35px !important;
    opacity: 0 !important;
    transition: 2s !important;
    font-weight: bold !important;
}

.portfolios-img-box:hover .visit_btn{
    opacity: 1 !important;
}

