#portfolio {
    padding: 75px 0;
}

.portfolio-title {
    display: flex;
}

.portfolio-title h1 {
    position: relative;
    z-index: 99;
    left: -20px;
    font-weight: bold;
    font-size: 28px;
    line-height: 33px;
    letter-spacing: 0.5em;
    color: #F6AE2D;
    font-family: 'Roboto', sans-serif;
}

.portfolio-title svg {
    position: relative;
    top: 20px;
}

.portfolio-title-extra {
    font-weight: 300;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    padding-left: 145px;
    position: relative;
    top: -4px;
    color: #F6AE2D;
}

.portfolios-gellary{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
    position: relative;
    z-index: 99;
    margin: 50px 0;
}

.portfolios-gellary a{
    display: block;
    width: 31.59%;
}

.portfolios-img-box{
    width: 100%;
    height: 450px;
    transition: ease-in-out 3s;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    z-index: 99;
    box-shadow: 0px 6px 18px 2px rgb(0 0 0 / 32%);
    display: grid;
    place-items: center;
}

.portfolios-img-box::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    background-color: #4e3d1c;
    opacity: 0;
    transition: ease-in-out 3s;
}

.portfolios-img-box:hover::before{
    opacity: .1;
}

.portfolios-img-box:hover{
    background-position: bottom;
    box-shadow: none;
}

.visit_btn{
    background-color: #F6AE2D !important;
    font-family: 'Montserrat', sans-serif !important;
    font-size: 12px !important;
    letter-spacing: 2px !important;
    color: #fff !important;
    padding: 10px 35px !important;
    opacity: 0 !important;
    transition: 2s !important;
    font-weight: bold !important;
}

.portfolios-img-box:hover .visit_btn{
    opacity: 1 !important;
}

@media screen and ( max-width: 1300px) {
    .portfolios-gellary a {
        width: 31%;
    }
}

@media screen and ( max-width: 990px) {
    .portfolios-gellary a {
        width: 45%;
    }
}

@media screen and ( max-width: 600px) {
    .portfolios-gellary a {
        width: 100%;
    }
}

@media screen and (min-width:1800px) {
    .portfolios-img-box{
        height: 650px;
    }
}